import React from 'react';
import { Section } from './Styles';
import CountUp from 'react-countup';

const Recovered = (props) => {
    const { value } = props.score;
    const value2 = <CountUp start={value - 100} end={value} duration={10} separator=" " />
    return (
        <Section>
            <h1>Liczba wyzdrowiałych</h1>
            <p>Do dnia {props.data} : {value2} osób</p>
        </Section>
    );
}

export default Recovered;