import React from 'react';
import './App.css';
import Deaths from './components/Deaths';
import Header from './components/Header';
import Recovered from './components/Recovered';
import Infected from './components/Infected';
import Mich from './components/michalowo';
import { fetchData } from './api/index';



const currentDate = new Date().toLocaleDateString()
class App extends React.Component {

  state = {
    numbers: {
      confirmed: {
        value: 0,
        detail: "",
      },
      deaths: {
        value: 0,
        detail: "",
      },
      recovered: {
        value: 0,
        detail: "",
      },

    }
  }
  async componentDidMount() {
    let numbers = await fetchData()
    this.setState({
      numbers
    })
  }
  render() {
    const { numbers } = this.state;

    return (
      <div className="App">
        <Header />
        <Infected data={currentDate} score={numbers.confirmed} />
        <Deaths data={currentDate} score={numbers.deaths} />
        <Recovered data={currentDate} score={numbers.recovered} />
        <Mich />
      </div>
    );
  }
}

export default App;
