import React from 'react';
import CountUp from 'react-countup';
import { Section } from './Styles';
import styled from 'styled-components';

const SectionDeath = styled(Section)`
border: 5px solid rgba(255, 255, 255, 0.5);
background-color: rgba(217, 72, 59, 0.712);
&:hover{
    background-color:rgba(217, 72, 59, 0.911);
}
& h1{
    color: rgba(255, 255, 255, 0.904);
}
& p{
    color: rgba(255, 255, 255, 0.801);
}
`;
const Deaths = (props) => {
    const { value } = props.score;
    const value2 = <CountUp start={value - 100} end={value} duration={10} separator=" " />
    return (

        <SectionDeath>
            <h1>Liczba zgonów</h1>
            <p>Do dnia {props.data} : <span>{value !== null ? value2 : "Ładowanie danych..."}</span> osób</p>
        </SectionDeath>
    );
}

export default Deaths;