import React from 'react';
import { Section } from './Styles';
import styled from 'styled-components';
import CountUp from 'react-countup';

const SectionInfected = styled(Section)`
border: 5px solid rgba(255, 255, 255, 0.507);
    background-color: rgba(74, 203, 217, 0.712);
&:hover{
    background-color: rgba(74, 203, 217, 0.904);
}
& h1{
    color: rgb(255, 255, 255);
}
& p{
    color: rgba(255, 255, 255, 0.897);
}
`;

const Infected = (props) => {
    const { value } = props.score;
    const value2 = <CountUp start={value} end={value + 15} duration={1000} separator=" " />
    return (
        <SectionInfected>
            <h1>Liczba zarażonych</h1>
            <p>Do dnia {props.data} : {value2} osób</p>
        </SectionInfected>
    );
}

export default Infected;