import React from 'react';
import CountUp from 'react-countup';

const Mich = () => {
    const value2 = <CountUp start={50} end={0} duration={20} separator=" " />
    return (
        <div style={{
            color: "white",
            fontSize: "18px",
            textTransform: "uppercase",
            margin: "10px",
            width: "auto",
            paddingBottom: "10px"
        }}>Zainfekowani w Michałowie: {value2}</div>
    );
}

export default Mich;
